import React, { useState } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from "@material-ui/core/MenuItem";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Menu from "@material-ui/core/Menu";
import { Link, useHistory } from 'react-router-dom';
import authReducer from '../services/authReducers';
import { action, runInAction } from 'mobx';
import { inject, observer } from 'mobx-react';
import {navBarStyles} from './componentStyles/navBarStyles'
const authManager = new authReducer()

const ButtonAppBar = inject("gigList", "profileList", "profile", "theme")(observer((props) => {
  
  const navBarStyles1= navBarStyles

  const classes = navBarStyles1();
  const history = useHistory();
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("xs"));

  const handleMenu = event => {

    setAnchorEl(event.currentTarget);
  };

  const handleMenuClick = (pageURL) => {
    // console.log(pageURL)

    if (pageURL === "dark-M") {
      props.theme.changeTheme()
      setAnchorEl(null);

    } else {

      history.push(pageURL);
      setAnchorEl(null);
    }
  };

  const handleLogOut = async () => {
    authManager.logOut()
    window.location.reload();
    // history.push("/")
    setAnchorEl(null);

  }

  const menuItems = [
    {
      menuTitle: "Feed",
      pageURL: "/"
    },
    {
      menuTitle: "Profile",
      pageURL: "/profilePage"
    },
    {
      menuTitle: "AddGig",
      pageURL: "/addGig"
    },
    {
      menuTitle: "MyGigs",
      pageURL: "/myGigs"
    },
    {
      menuTitle: "Logout",
      pageURL: "/"
    },
    {
      menuTitle: "Chat",
      pageURL: "/chat"
    },
    {
      menuTitle: "dark-M",
      pageURL: "dark-M"

    },

  ];

  return (
    <div className={classes.rootp}>
      <AppBar position="static" className={props.theme.theme ? classes.darkBar : classes.bar}>
        <Toolbar>
          <span onClick={() => history.push('/')}>

            <Typography variant="h4" className={props.theme.theme ? classes.darkTitle : classes.title}>
              G
           {/* <img className={classes.logo} src="data:image/svg+xml;base64," /> */}
              <img className={props.theme.theme ? classes.darkLogo : classes.logo} src="data:image/svg+xml;base64," />
            G
          </Typography >
            <Typography variant="h5" className={props.theme.theme ? classes.darkTitle1 : classes.title1}>
              me
          </Typography>
          </span>


          <IconButton
            edge="start"
            position="static"
            className={props.theme.theme ? classes.darkMenuButton : classes.menuButton}
            color="inherit"
            aria-label="menu"
            onClick={handleMenu}
          >
            <MenuIcon />
          </IconButton>
          <Menu
            
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: "top",
              horizontal: "right"
            }}
            keepMounted
            transformOrigin={{
              vertical: "top",
              horizontal: "right"
            }}
            open={open}
            onClose={() => setAnchorEl(null)}
          >
            {menuItems.map((menuItem, index) => {
              const { menuTitle, pageURL } = menuItem;
              if (menuTitle === "Logout") {
                return (
                  <MenuItem key={index} onClick={handleLogOut}>
                    {menuTitle}
                  </MenuItem>
                );
              }
              else {
                return (
                  <MenuItem key={index}  onClick={() => handleMenuClick(pageURL)}>
                    {menuTitle}
                  </MenuItem>
                );
              }
            })}
          </Menu>

        </Toolbar>
      </AppBar>
    </div>
  );
}))

export default ButtonAppBar
