
import { Box, Button, Container, CssBaseline, Grid, makeStyles, TextField, Typography } from '@material-ui/core';
import { Copyright } from '@material-ui/icons';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios'
import { useHistory } from "react-router-dom";
import {loginStyles} from './componentStyles/loginStyles'

const LogIn = (props) => {
  const useStyles1= loginStyles
  
  const classes = useStyles1();

  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const history = useHistory();

  const handleEmail = (e) => {
    setEmail(e.target.value)
  }

  const handlePassword = (e) => {
    setPassword(e.target.value)
  }

  const handleSubmit = async function (e) {
    e.preventDefault()

    const res = await axios.post(`${process.env.REACT_APP_SERVER_URL}/logIn`, { email: email, password: password })
    // "http://localhost:3001/logIn"
    localStorage.setItem('auth-token', res.data.token)
    props.handleLogIn()
    // history.push("/");
  }

  return (
    <div className="App">
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <div className={classes.paper}>
          <img className={classes.icon} src="data:image/svg+xml;base64,"/>
         
          <Typography component="h1" variant="h5">
            Welcome to GigMe 
          </Typography>
          <form className={classes.form} noValidate>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              autoFocus
              value={email}
              onChange={handleEmail}
            />

            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
              value={password}
              onChange={handlePassword}
            />

            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              onClick={handleSubmit}
            >
              Sign in
            </Button>

            <Link to='/signUp'>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
              >
                CREATE ACCOUNT
            </Button>
            </Link>


          </form>
        </div>
        <Box mt={8}>
          <Copyright />
        </Box>
      </Container>
    </div>
  );
}

export default LogIn;
